export default {
    data() {
        return {
            value: ''
        }
    },
    components: {
        
    },
    beforeCreate(){
        this.$store.dispatch('shopdetail', this.$route.query.shopId)
    },
    computed: {
        shopdetail(){
            return this.$store.state.shopdetail
        }
    },
    created(){
        
    },
    methods: {
        result(){
            let list = { id: parseInt(this.$route.query.shopId), antifakeNo: this.value }
            this.$store.dispatch('bindings', list)
        }
    },
}



